export default {
	data() {
		return {
			index:-1,
			list: [],
			// currData: null
		}
	},
	onLoad(option) {
		this.index = option.newsId
			uni.request({
				url: '../../static/data/news.json',
				method: 'GET',
				data: {},
				success: (res) => {
					this.list = res.data.data
					// this.currData = this.list[this.index].content
				},
				fail: (err) => { console.log(err)},
				complete: () => {}
			})			
	},
	methods: {
		
	}
}
